@import url('https://fonts.googleapis.com/css2?family=Cormorant:ital,wght@0,300..700;1,300..700&family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Play:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant:ital,wght@0,300..700;1,300..700&family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Play:wght@400;700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

:root {
  --font-main: "Cormorant", serif;
  --font-accent: "Raleway", sans-serif;
  --padding-main: 30px 20px 30px 20px;
}

* {
  margin: 0;
  padding: 0;
  background: none repeat scroll 0 0 transparent;
  border: medium none;
  border-spacing: 0;
}

.main-first {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  background: url(/src/image/1.jpg) no-repeat center center / cover;
}

li {
  text-decoration: none;
  list-style: none;
}

.header {
  position: sticky;
  font-family: var(--font-accent);
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  padding: var(--padding-main);
}

.header__wrapper-item {
  font-size: 20px;
  color: #4C2F27;
}

.main {
  padding-bottom: 30px;
  font-size: 50px;
  font-family: var(--font-main);
  font-weight: 500;
  color: #4C2F27;
  text-transform: uppercase;
  padding: var(--padding-main);
}

.accent {
  font-style: italic;
}

.section__what {
  display: flex;
  flex-direction: column;
  text-align: center;
  /* justify-content: center; */
  color: #4C2F27;
  background-color: #ffffff;
  gap: 50px;
  font-family: var(--font-main);
  font-size: 20px;
  align-items: center;
  padding: var(--padding-main);
}
.section__what-title{

}


.section__what-button {
  padding: 20px;
  color: #4C2F27;
  border: #4C2F27 solid 2px;
  border-radius: 20px;
  font-family: var(--font-main);
  font-size: 20px;
  font-weight: 600;
  width: 390px;
  transition: 3s;
}